import React, { Component } from 'react'
import './css.css';
import utils from '../../library'

class Cards extends Component {
   
    render(){
        utils.setHomeColors()
        console.log(this.props.update) 
        return ( 
        <div>

        <div id="cards_flyer"><div id="cards_mainlabel">Cypher's Cards Collections</div>
            <img id="cards_imageMain"  alt="NFT"  src="https://cdn.discordapp.com/attachments/751252665085984882/825465842678235176/cybs_back.png"  onClick={() => this.props.update("Features")}></img>
            <br></br>
            <div className="cards_button" onClick={() => this.props.update("Features")}>🃏 CYPHERCARDS</div>
           
            <div id="cards_sublabel"></div>
        </div>
      
        </div>
        );
    }


}




export default Cards